import DataQaUtils from "./utils/data-qa-utils";
import { CssClasses } from "./css-classes";
export const PagerSizeSelectorCssClass = CssClasses.Prefix + "-pager-page-size-selector";
class DxPager extends HTMLElement {
    connectedCallback() {
        DataQaUtils.setLoaded(this);
    }
    disconnectedCallback() {
        DataQaUtils.removeLoaded(this);
    }
}
customElements.define("dxbl-pager", DxPager);
function loadModule() { }
export default { loadModule };
